.container {
    position: relative;
    height: 70vh;
}

.bg_image {
    position: absolute;
    top: -30px;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../../../assets/agile.png'); /* Replace with your image URL */
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.2;
    z-index: -1;
}

.content {
    display: block;
    justify-content: center;
    align-items: center;
    height: 100%;
}



